import "./Student.css";
import Tags from "./Tags/Tags";

export const StudentProfile = ({
  fullName,
  email,
  company,
  skill,
  id,
  average,
  testGrades,
  tags,
  toggleState,
}) => {
  return (
    <section className="student-container">
      <header className="fullname">{fullName}</header>
      <article className="profile">
        <p>Email: {email}</p>
        <p>Company: {company}</p>
        <p>Skill: {skill}</p>
        <p data-testid={`average-${id}`}>Average: {average}%</p>
      </article>
      <aside
        data-testid={`grade-list-${id}`}
        className={
          toggleState ? "test-container-expanded" : "test-container-collapsed"
        }
      >
        <ul>{testGrades}</ul>
      </aside>
      <Tags id={id} tags={tags} />
    </section>
  );
};
