import * as React from "react";
import "./Student.css";
import { Avatar } from "./Avatar";
import { ToggleGradesButton } from "./ToggleGradesButton";
import { StudentProfile } from "./StudentProfile";

const Student = ({
  pic,
  fullName,
  email,
  company,
  skill,
  grades,
  id,
  tags,
  toggleState,
}) => {
  const average =
    grades.map(Number).reduce((acc, init) => (acc = acc + init), 0) /
    grades.length;

  const testGrades = grades.map((grade, index) => {
    return (
      <li data-testid={`grade-${id}`} key={index} className="test-container">
        <span className="testNumber">{`Test ${index + 1}:`}</span>
        <span className="testValue">{`${grade}%`}</span>
      </li>
    );
  });

  return (
    <main data-testid={`student-${id}`} className="card">
      <Avatar pic={pic} />
      <StudentProfile
        fullName={fullName}
        email={email}
        company={company}
        skill={skill}
        id={id}
        average={average}
        testGrades={testGrades}
        tags={tags}
        toggleState={toggleState}
      />
      <ToggleGradesButton id={id} toggleState={toggleState} />
    </main>
  );
};

export default Student;
